import React, {Component} from "react";
import PropTypes from "prop-types";
import styled, {css} from "styled-components";
import {Box, Container, Flex, theme} from "@mechatronick/datum-design-system";
import Icon from "@hackclub/icons";
import Flag from "../Flag";
import {Link} from "gatsby";
import ScrollLock from "react-scrolllock";

const rgbaBgColor = (props, opacity) =>
    `rgba(
    ${props.bgColor[0]},
    ${props.bgColor[1]},
    ${props.bgColor[2]},
    ${opacity}
  )`;

const Root = styled(Box.withComponent("header"))` 
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1000;
  transition: all 0.4s ease;
  ${props =>
    (props.scrolled || props.toggle || props.fixed) &&
    css`
      transition: all 0.4s ease;
      position: fixed;
      background-color: ${rgbaBgColor(props, 0.96875)};
      border-bottom: 1px solid rgba(48, 48, 48, 0.125);
      @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
        background-color: ${props.transparent
        ? "transparent"
        : rgbaBgColor(props, 0.75)};
        ${props.dark
        ? css`
              -webkit-backdrop-filter: saturate(90%) blur(20px);
            `
        : css`
              -webkit-backdrop-filter: saturate(180%) blur(20px);
            `};
      }
      ${theme.mediaQueries.reduceTransparency} {
        -webkit-backdrop-filter: initial !important;
      }
    `} 
  }
`;

const Content = styled(Container)`
  ${props =>
    props.isMobile
        ? css`
          display: flex;
          align-items: center;
          justify-content: space-between;
          position: relative;
          padding-left: ${theme.space[3]}px;
          ${theme.mediaQueries.md} {
            padding: 0 ${theme.space[4]}px;
          }
        `
        : null}
`;

