const NavBar = styled(Box.withComponent("nav"))`
  display: none;
  a {
    margin-left: ${theme.space[1]}px;
    margin-right: ${theme.space[1]}px;
    padding: ${theme.space[1]}px;
    text-decoration: none;
    letter-spacing: 0;
  }
  ${props =>
    props.isMobile
        ? css`
          display: ${props.toggle ? "flex" : "none"};
          opacity: ${props.toggle ? 1 : 0};
          flex-direction: column;
          overflow-y: auto;
          text-align: left;
          height: 100vh;
          a {
            color: ${theme.colors["primary"]};
            border-bottom: 1px solid rgba(48, 48, 48, 0.125);
            &:last-child {
              color: ${theme.colors["accent"]};
              border-bottom: none;
            }
            padding: 0;
            margin: 0 auto;
            height: 64px;
            line-height: 64px;
            text-transform: uppercase;
            font-weight: normal;
            width: 100%;
            max-width: 18rem;
            text-shadow: 0 0 1px;
            transition: all 0.4s ease;
            @media screen and (max-width: 22em) {
              max-width: 16rem;
            }
            &:hover {
              opacity: 1;
              text-shadow: none;
              border-bottom: 4px solid;
              transition: all 0.4s ease;
              transform: translate3D(0, -2px, 0);
            }
          }
        `
      : css`
          text-align: center;
          ${theme.mediaQueries.md} {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-end;
            font-size: ${theme.fontSizes[0]}px;
          }
          ${theme.mediaQueries.lg} {
            font-size: ${theme.fontSizes[2]}px;
          }
          a:not(:last-child):not(:first-child) {
            text-transform: uppercase;
            transition: all 0.4s ease;
            text-decoration: none;
            color: black;
            &:hover {
              opacity: 1;
              color: ${theme.brandColors.primary};
              border-bottom: 4px solid ${theme.brandColors.primary};
              transition: all 0.4s ease;
              transform: translate3D(0, -2px, 0);
            }
          }
          a:last-child {
            text-transform: uppercase;
            transition: all 0.4s ease;
            text-decoration: none;
            color: ${theme.colors.trueWhite};
            background: ${theme.brandColors.accent};
            border: 4px solid ${theme.brandColors.accent};
            &:hover {
              opacity: 1;
              color: ${theme.brandColors.accent};
              background: ${theme.colors.trueWhite};
              border: 4px solid ${theme.brandColors.accent};
              transition: all 0.4s ease;
            }
          }
        `};
`;

