const ToggleContainer = styled(Flex)`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  min-width: 64px;
  min-height: 48px;
  cursor: pointer;
  user-select: none;
  ${theme.mediaQueries.md} {
    cursor: auto;
    pointer-events: none;
    display: none;
  }
`;

const Toggle = styled(Icon)`
  margin: 0 ${theme.space[2]}px;
  display: block;
  ${theme.mediaQueries.md} {
    display: none;
  }
`;

class Nav extends Component {
    static defaultProps = {
        // use pair of true+white and false+black
        dark: false,
        color: "black",
    };
    static propTypes = {
        color: PropTypes.string,
        bgColor: PropTypes.arrayOf(PropTypes.number),
        dark: PropTypes.bool,
        transparent: PropTypes.bool,
        fixed: PropTypes.bool,
        toggle: PropTypes.bool,
    };
    state = {
        scrolled: false,
        toggle: false,
    };

    componentDidMount() {
        this.bindScroll(true);
        if (typeof window !== "undefined") {
            const mobileQuery = window.matchMedia("(max-width: 48em)");
            mobileQuery.addListener(() => {
                this.setState({toggle: false});
            });
        }
    }

    componentWillUnmount = () => {
        this.bindScroll(false);
    };

    bindScroll = add => {
        if (typeof window !== "undefined") {
            window[add ? "addEventListener" : "removeEventListener"](
                "scroll",
                this.onScroll
            );
        }
    };

    onScroll = () => {
        const newState = window.scrollY >= 200;
        const {scrolled: oldState} = this.state;

        if (newState !== oldState) {
            this.setState({
                scrolled: newState,
            });
        }
    };

    handleToggleMenu = () => {
        this.setState(state => ({
            toggle: !state.toggle,
        }));
    };

    render() {
        const {color, dark, fixed, bgColor, ...props} = this.props;
        const {scrolled, toggle} = this.state;
        const baseColor = dark
            ? color || "white"
            : color === "white" && scrolled
                ? "black"
                : color;
        const toggleColor = dark
            ? color || "snow"
            : toggle || (color === "white" && scrolled)
                ? "slate"
                : color;

        return (
            <Root
                {...props}
                fixed={fixed}
                scrolled={scrolled}
                toggle={toggle}
                dark={dark}
                bgColor={bgColor || (dark ? [32, 34, 36] : [255, 255, 255])}
            >
                <Content>
                    <Navigation color={baseColor} dark={dark}/>
                    <ToggleContainer color={toggleColor} onClick={this.handleToggleMenu}>
                        <Flag/>
                        <div style={{marginLeft: "auto"}}/>
                        <Toggle
                            glyph={toggle ? "view-close" : "menu"}
                            toggle={toggle ? 1 : 0}
                        />
                    </ToggleContainer>
                </Content>
                <MobileNav
                    isMobile
                    toggle={toggle ? 1 : 0}
                    color={baseColor}
                    tdark={dark}
                />
                {toggle && <ScrollLock/>}
            </Root>
        );
    }
}

export default Nav;
