import { theme } from "@mechatronick/datum-design-system";
import styled, { css } from "styled-components";
import { graphql, Link, useStaticQuery } from "gatsby";
import React from "react";

const InnerFlag = styled(Link)`
  background: url(${props => props.src}) no-repeat center;
  flex-shrink: 0;
  width: 144px;
  height: 62px;
  transform: scale(0.75);
  transition: ${theme.transition} transform;
  transform-origin: center center;
  ${theme.mediaQueries.md} {
    transform: scale(1);
    width: 144px;
    height: 72px;
  }
  ${props =>
    props.scrolled &&
    css`
      transform: scale(0.75);
      height: 44px !important;
      ${theme.mediaQueries.md} {
        height: 54px !important;
      }
    `};
`;

InnerFlag.defaultProps = {
  to: "/",
  "aria-label": "Homepage",
};

export default () => {
  const { navLogo } = useStaticQuery(
    graphql`
      query {
        navLogo: file(relativePath: { eq: "largeIcon.png" }) {
          childImageSharp {
            fluid(maxWidth: 144) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  );
  return <InnerFlag src={navLogo.childImageSharp.fluid.src} />;
};
