import React, { Component } from "react";
import StyledDivWithCoverControlledDimensions from "../StyledDivWithCoverControlledDimensions";
import TriangleEven from "../TriangleEven";
import { Box, Field, Flex, Heading, Text, theme } from "@mechatronick/datum-design-system";
import styled from "styled-components";
import PropTypes from "prop-types";
import FlexWrapper from "../FlexWrapper";
import ButtonNoLink from "../ButtonNoLink";
import Member from "../Member";
import helperLib from "../../helperLib";
import { useMemberPicsHook } from "../../teamMembersPics";
import MapSVG from "../../images/map.svg";
import Pulse from "react-reveal/Pulse";
// import Fade from "react-reveal/Fade";

const LowerDiv = styled.div`
  position: ${props => props.position};
  z-index: 2;
  top: ${props => props.top[0]}vh;
  ${theme.mediaQueries.md} {
    top: ${props => props.top[1]}vh;
  }
  ${theme.mediaQueries.lg} {
    top: ${props => props.top[2]}vh;
  }
`;

LowerDiv.propsTypes = {
  top: PropTypes.arrayOf(PropTypes.number).isRequired,
};

const SEND_STATUS = {
  NULL: null,
  SENDING: "SENDING",
  ERROR: "ERROR",
  SUCCESS: "SUCCESS",
};

class ContactFormSubmit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: SEND_STATUS.NULL,
      connectionError: false,
      displayInputErrorText: false,
      formData: {
        firstName: "",
        lastName: "",
        email: "",
        phone: "",
        industry: "",
        agenda: "",
        enquiry: "",
      },
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputChange(event) {
    const { name, value } = event.target;
    this.setState((state) => {
        let newState = { ...state };
        newState.formData[name] = value;
        return newState;
      },
    );
  }

  handleSubmit() {
    const { firstName, lastName, email, enquiry } = this.state.formData;
    const firstNameOk = helperLib.atLeastTwoCharactersAndOnlyCharacter(firstName);
    const lastNameOk = helperLib.atLeastTwoCharactersAndOnlyCharacter(lastName);
    const emailOk = helperLib.oldValidEmail(email);
    const enquiryOk = helperLib.checkIfNotNullUndefinedEmpty(enquiry) && enquiry.length > 2;
    if (firstNameOk && lastNameOk && emailOk && enquiryOk) {
      this.setState({
          displayInputErrorText: false,
          status: SEND_STATUS.SENDING,
        },
        () => {
          fetch("https://4pleirljz8.execute-api.ap-southeast-2.amazonaws.com/default/datumWebSendEmail", {
            method: "POST",
            body: JSON.stringify(this.state.formData),
            headers: {
              "Content-Type": "application/json",
            },
          })
            .then((resp) => {
              return resp.json();
            })
            .then((response) => {
              console.log(response);
              if (response === null || response.statusCode !== "FAKE_EMAIL_USED") {
                this.setState({ status: SEND_STATUS.SUCCESS });
              } else {
                throw Error(response.body);
              }
            })
            .catch((err) => {
              this.setState({ status: SEND_STATUS.ERROR });
              console.log(err);
            });
        });
    } else {
      this.setState({
        displayInputErrorText: true,
        status: SEND_STATUS.ERROR,
      });
    }
  }

  render() {
    const { status } = this.state;
    return (
      <>
        {status !== SEND_STATUS.SUCCESS && (
          <>
            <Box width={[1, 1 / 2, 1 / 2]} p={[2, 2, 3]}>
              <Field
                type={"text"}
                name={"firstName"}
                label={"First name"}
                placeholder={"Your first name"}
                error={"Required"}
                onChange={this.handleInputChange}
              />
            </Box>
            <Box width={[1, 1 / 2, 1 / 2]} p={[2, 2, 3]}>
              <Field
                type={"text"}
                name={"lastName"}
                label={"Last name"}
                placeholder={"Your last name"}
                error={"Required"}
                onChange={this.handleInputChange}
              />
            </Box>
            <Box width={[1, 1 / 2, 1 / 2]} p={[2, 2, 3]}>
              <Field
                type={"email"}
                name={"email"}
                label={"Business email"}
                placeholder={"hello@business.com"}
                error={"Required"}
                onChange={this.handleInputChange}
              />
            </Box>
            <Box width={[1, 1 / 2, 1 / 2]} p={[2, 2, 3]}>
              <Field
                type={"tel"}
                name={"phone"}
                label={"Phone number"}
                placeholder={"+(064) XXX XXXXX"}
                onChange={this.handleInputChange}
              />
            </Box>
            <Box width={[1, 1 / 2, 1 / 2]} p={[2, 2, 3]}>
              <Field
                name="industry"
                type="select"
                label="Your industry"
                onChange={this.handleInputChange}
              >
                <option>Choose one</option>
                <option>Aerospace</option>
                <option>Agriculture</option>
                <option>Banking</option>
                <option>Chemical</option>
                <option>Construction</option>
                <option>Defense</option>
                <option>Education</option>
                <option>Energy</option>
                <option>Entertainment</option>
                <option>Financial</option>
                <option>Financial services</option>
                <option>Food</option>
                <option>Health care</option>
                <option>Hospitality</option>
                <option>Information Technology</option>
                <option>Manufacturing</option>
                <option>Mass media</option>
                <option>Mining</option>
                <option>Telecommunications</option>
                <option>Transport</option>
                <option>Water</option>
                <option>Other</option>
              </Field>
            </Box>
            <Box width={[1, 1 / 2, 1 / 2]} p={[2, 2, 3]}>
              <Field
                name="agenda"
                type="select"
                label="Agenda"
                onChange={this.handleInputChange}
              >
                <option>Choose one</option>
                <option>New project</option>
                <option>Existing project</option>
                <option>Let's have a chat</option>
                <option>Tell me more about</option>
                <option>Careers</option>
                <option>Other</option>
              </Field>
            </Box>
            <Box width={[1, 1, 1]} p={[2, 2, 3]}>
              <Field
                type={"textarea"}
                name={"enquiry"}
                label={"How can we help?"}
                placeholder={"TELL US ABOUT YOUR PROJECT"}
                error={"Required"}
                onChange={this.handleInputChange}
              />
            </Box>
          </>
        )}
        <Box
          width={[1, 1, 1]}
          px={[2, 2, 3]}
          style={{ textAlign: "center" }}
        >
          {status !== SEND_STATUS.SUCCESS && (
            <Text>
              By submitting, I agree with Datum Consulting's Privacy Policies
            </Text>
          )}
          {status === SEND_STATUS.NULL && (
            <ButtonNoLink
              onClick={this.handleSubmit}
            >
              SUBMIT
            </ButtonNoLink>
          )
          }
          {status === SEND_STATUS.SENDING && (
            <ButtonNoLink
              color={"lightAccent"}
            >
              LOADING
            </ButtonNoLink>
          )
          }
          {status === SEND_STATUS.ERROR && (
            <ButtonNoLink
              onClick={this.handleSubmit}
              color={"darkAccent"}
            >
              ERROR! TRY AGAIN
            </ButtonNoLink>
          )
          }
          {status === SEND_STATUS.SUCCESS && (
            <>
              <Text>
                Allow us with time to reply to your enquiry.
              </Text>
              <Text>
                Datum Consulting NZ team
              </Text>
              <ButtonNoLink
                color={"successGreen"}
                disable
              >
                THANK YOU
              </ButtonNoLink>
            </>
          )
          }
        </Box>
        <Box
          width={[1, 1, 1]}
          px={[2, 2, 3]}
          f={1}
          color={"errorRed"}
          style={{ display: this.state.displayInputErrorText ? "block" : "none" }}
        >
          <Text>
            Incorrect input:
          </Text>
          <Text>
            - Names: only characters, more than 2 characters
          </Text>
          <Text>
            - Email: correct email
          </Text>
          <Text>
            - Enquiry: only characters, more than 2 characters
          </Text>
        </Box>
      </>
    );
  }
}

export default () => {
  const NickDo = useMemberPicsHook().NickDo.childImageSharp.fluid.src;
  return (
    <>
      <LowerDiv position={"relative"} top={[5, 7.5, 10]}>
        <Pulse>
          <Heading.h1
            style={{ textAlign: "center" }}
          >Contact us</Heading.h1>
        </Pulse>
      </LowerDiv>
      <StyledDivWithCoverControlledDimensions
        smallScreenHeight={[60, 100]}
        mediumScreenHeight={[70, 70]}
        largeScreenHeight={[70, 70]}
        backgroundImageUrl={MapSVG}
        backgroundPosition={"right bottom"}
        backgroundSize={"cover"}
        minHeight
      >
        <TriangleEven
          direction="down"
          color="trueWhite"
          opacity={1}
          height={[10, 15, 20]}
          width={[100, 100, 100]}
        />
        <LowerDiv
          position={"relative"}
          top={[7.5, 10, 12.5]}
          style={{ margin: `${theme.space[2]}px`, marginBottom: `20vh` }}
        >
          {/*<Fade delay={300}>*/}
          <FlexWrapper wrap={"wrap"} alignItems={"center"}>
            <Box
              width={[1, 1, 1 / 3]}
              my={[3, 0, 0]}
              style={{
                textAlign: "center",
              }}
            >
              <Member
                alt={"Nick Do"}
                src={NickDo}
                textMaxWidth={"100px"}
                name={"Nick Do"}
                position={"Co-founder"}
              />
            </Box>
            <Flex
              width={[1, 1, 2 / 3]}
              p={[2, 2, 3]}
              style={{
                boxShadow: `${theme.boxShadows[1]}`,
                backgroundColor: "rgba(249,249,250,0.85)",
                textAlign: "left",
                flexWrap: "wrap",
              }}
            >
              <ContactFormSubmit/>
            </Flex>
          </FlexWrapper>
          {/*</Fade>*/}
        </LowerDiv>
      </StyledDivWithCoverControlledDimensions>
    </>
  );
};
