import styled, { css } from "styled-components";
import PropTypes from "prop-types";
import { theme } from "@mechatronick/datum-design-system";

const TriangleEven = styled.div`
  position: absolute;
  width: 0;
  height: 0;
  z-index: 1;

  opacity: ${props => (props.opacity === undefined ? 0.8 : props.opacity)};
  ${props => {
    const width = props.width[0];
    const height = props.height[0];
    const color = theme.colors[props.color] || props.color;
    if (props.direction === "up") {
      return css`
        border-left: ${width / 2}vw solid transparent;
        border-right: ${width / 2}vw solid transparent;
        border-bottom: ${height}vh solid ${color};
      `;
    } else if (props.direction === "down") {
      return css`
        border-left: ${width / 2}vw solid transparent;
        border-right: ${width / 2}vw solid transparent;
        border-top: ${height}vh solid ${color};
      `;
    } else if (props.direction === "pointing left") {
      return css`
        border-top: ${height / 2}vh solid transparent;
        border-right: ${width}vw solid ${color};
        border-bottom: ${height / 2}vh solid transparent;
      `;
    } else if (props.direction === "pointing right") {
      return css`
        border-top: ${height / 2}vh solid transparent;
        border-left: ${width}vw solid ${color};
        border-bottom: ${height / 2}vh solid transparent;
      `;
    }
  }}
  ${theme.mediaQueries.md} {
    ${props => {
      const width = props.width[1];
      const height = props.height[1];
      const color = theme.colors[props.color] || props.color;
      if (props.direction === "up") {
        return css`
          border-left: ${width / 2}vw solid transparent;
          border-right: ${width / 2}vw solid transparent;
          border-bottom: ${height}vh solid ${color};
        `;
      } else if (props.direction === "down") {
        return css`
          border-left: ${width / 2}vw solid transparent;
          border-right: ${width / 2}vw solid transparent;
          border-top: ${height}vh solid ${color};
        `;
      } else if (props.direction === "pointing left") {
        return css`
          border-top: ${height / 2}vh solid transparent;
          border-right: ${width}vw solid ${color};
          border-bottom: ${height / 2}vh solid transparent;
        `;
      } else if (props.direction === "pointing right") {
        return css`
          border-top: ${height / 2}vh solid transparent;
          border-left: ${width}vw solid ${color};
          border-bottom: ${height / 2}vh solid transparent;
        `;
      }
    }}
  }
  ${theme.mediaQueries.lg} {
    ${props => {
      const width = props.width[2];
      const height = props.height[2];
      const color = theme.colors[props.color] || props.color;
      if (props.direction === "up") {
        return css`
          border-left: ${width / 2}vw solid transparent;
          border-right: ${width / 2}vw solid transparent;
          border-bottom: ${height}vh solid ${color};
        `;
      } else if (props.direction === "down") {
        return css`
          border-left: ${width / 2}vw solid transparent;
          border-right: ${width / 2}vw solid transparent;
          border-top: ${height}vh solid ${color};
        `;
      } else if (props.direction === "pointing left") {
        return css`
          border-top: ${height / 2}vh solid transparent;
          border-right: ${width}vw solid ${color};
          border-bottom: ${height / 2}vh solid transparent;
        `;
      } else if (props.direction === "pointing right") {
        return css`
          border-top: ${height / 2}vh solid transparent;
          border-left: ${width}vw solid ${color};
          border-bottom: ${height / 2}vh solid transparent;
        `;
      }
    }}
  }
`;

TriangleEven.propTypes = {
  direction: PropTypes.oneOf(["up", "down", "pointing left", "pointing right"]),
  height: PropTypes.arrayOf(PropTypes.number).isRequired,
  width: PropTypes.arrayOf(PropTypes.number).isRequired,
  opacity: PropTypes.number,
  color: PropTypes.string,
};

export default TriangleEven;
